exports.push([module.id, "\n.table-content{\n    min-height: 600px;\n    background: #fff;\n}\n.search-box{\n    margin-bottom: 20px;\n}\n.highlight {\nbackground-color: rgb(255, 192, 105);\npadding: 0px;\n}\n", "", {"version":3,"sources":["D:/demo/ChartsDemo/src/components/src/components/Table.vue"],"names":[],"mappings":";AA8MA;IACA,kBAAA;IACA,iBAAA;CACA;AACA;IACA,oBAAA;CACA;AACA;AACA,qCAAA;AACA,aAAA;CACA","file":"Table.vue","sourcesContent":["<template>\r\n    <div class=\"table-content\">\r\n        <a-input-search class=\"search-box\" placeholder=\"请输入查询内容\" enter-button @search=\"onSearch\" />\r\n\r\n        <a-table :columns=\"columns\" :data-source=\"data\" :loading=\"loading\">\r\n            <div \r\n                slot=\"filterDropdown\"\r\n                slot-scope=\"{ setSelectedKeys, selectedKeys, confirm, clearFilters, column }\"\r\n                style=\"padding: 8px\"\r\n            >\r\n                <a-input\r\n                    v-ant-ref=\"c => (searchInput = c)\"\r\n                    :placeholder=\"`搜索 ${column.title}`\"\r\n                    :value=\"selectedKeys[0]\"\r\n                    style=\"width: 188px; margin-bottom: 8px; display: block;\"\r\n                    @change=\"e => setSelectedKeys(e.target.value ? [e.target.value] : [])\"\r\n                    @pressEnter=\"() => handleSearch(selectedKeys, confirm, column.dataIndex)\"\r\n                />\r\n                <a-button\r\n                    type=\"primary\"\r\n                    icon=\"search\"\r\n                    size=\"small\"\r\n                    style=\"width: 90px; margin-right: 8px\"\r\n                    @click=\"() => handleSearch(selectedKeys, confirm, column.dataIndex)\"\r\n                >\r\n                    搜索\r\n                </a-button>\r\n                <a-button size=\"small\" style=\"width: 90px\" @click=\"() => handleReset(clearFilters)\">\r\n                    清空\r\n                </a-button>\r\n            </div>\r\n            <a-icon\r\n                slot=\"filterIcon\"\r\n                slot-scope=\"filtered\"\r\n                type=\"search\"\r\n                :style=\"{ color: filtered ? '#108ee9' : undefined }\"\r\n            />\r\n            <span slot=\"tags\" slot-scope=\"tags\">\r\n                <a-tag\r\n                    v-for=\"tag in tags\"\r\n                    :key=\"tag\"\r\n                    :color=\"tag === 'loser' ? 'volcano' : tag.length > 5 ? 'geekblue' : 'green'\"\r\n                >\r\n                    {{ tag.toUpperCase() }}\r\n                </a-tag>\r\n            </span>\r\n            <span slot=\"gender\" slot-scope=\"gender\">{{gerder}}</span>\r\n            <span slot=\"action\" slot-scope=\"record\">\r\n                <a>查看 一 {{ record.name }}</a>\r\n                <a-divider type=\"vertical\" />\r\n                <a>编辑</a>\r\n                <a-divider type=\"vertical\" />\r\n                <!-- <a class=\"ant-dropdown-link\"> More actions <a-icon type=\"down\" /> </a> -->\r\n                <a-dropdown :trigger=\"['click']\">\r\n                    <a class=\"ant-dropdown-link\" @click=\"e => e.preventDefault()\">\r\n                    更多操作 <a-icon type=\"down\" />\r\n                    </a>\r\n                    <a-menu slot=\"overlay\" @click=\"onClick\">\r\n                        <a-menu-item key=\"1\">\r\n                            添加标签\r\n                        </a-menu-item>\r\n                        <a-menu-item key=\"2\">\r\n                            复制新增\r\n                        </a-menu-item>\r\n                        <a-menu-item key=\"3\">\r\n                            删除\r\n                        </a-menu-item>\r\n                    </a-menu>\r\n                </a-dropdown>\r\n            </span>\r\n            <p slot=\"expandedRowRender\" slot-scope=\"record\" style=\"margin: 0\">\r\n                {{ record.description }}\r\n            </p>\r\n             <template slot=\"customRender\" slot-scope=\"text, record, index, column\">\r\n                <span v-if=\"searchText && searchedColumn === column.dataIndex\">\r\n                    <template\r\n                    v-for=\"(fragment, i) in text\r\n                        .toString()\r\n                        .split(new RegExp(`(?<=${searchText})|(?=${searchText})`, 'i'))\"\r\n                    >\r\n                    <mark\r\n                        v-if=\"fragment.toLowerCase() === searchText.toLowerCase()\"\r\n                        :key=\"i\"\r\n                        class=\"highlight\"\r\n                        >{{ fragment }}</mark\r\n                    >\r\n                    <template v-else>{{ fragment }}</template>\r\n                    </template>\r\n                </span>\r\n                <template v-else>\r\n                    {{ text }}\r\n                </template>\r\n            </template>\r\n        </a-table>\r\n    </div>\r\n</template>\r\n<script>\r\n\r\nconst columns = [\r\n    {\r\n        title: '姓名', \r\n        dataIndex: 'name', \r\n        key: 'name', \r\n        scopedSlots: {\r\n            filterDropdown: 'filterDropdown',\r\n            filterIcon: 'filterIcon',\r\n            customRender: 'customRender',\r\n        },\r\n        onFilter: (value, record) =>\r\n            record.name\r\n              .toString()\r\n              .toLowerCase()\r\n              .includes(value.toLowerCase()),\r\n        onFilterDropdownVisibleChange: visible => {\r\n            if (visible) {\r\n              setTimeout(() => {\r\n                this.searchInput.focus();\r\n              }, 10);\r\n            }\r\n        },\r\n    },\r\n    {\r\n        title: '性别',\r\n        dataIndex: 'gender',\r\n        key: 'gender',\r\n        filters: [\r\n            { text: '男', value: 0 },\r\n            { text: '女', value: 1 },\r\n        ],\r\n    },\r\n    {\r\n        title: '年龄',\r\n        dataIndex: 'age',\r\n        key: 'age'\r\n    },\r\n    {\r\n        title: '标签',\r\n        dataIndex: 'tags',\r\n        key: 'tags',\r\n        scopedSlots: {\r\n            customRender: 'tags'\r\n        }\r\n    },\r\n    {\r\n        title: '地址',\r\n        dataIndex: 'address',\r\n        key: 'address',\r\n        scopedSlots: {\r\n            filterDropdown: 'filterDropdown',\r\n            filterIcon: 'filterIcon',\r\n            customRender: 'customRender',\r\n        },\r\n        onFilter: (value, record) => \r\n        record.address\r\n              .toString()\r\n              .toLowerCase()\r\n              .includes(value.toLowerCase()),\r\n    },\r\n    {\r\n        title: '操作',\r\n        dataIndex: '',\r\n        key: 'x',\r\n        scopedSlots: {\r\n            customRender: 'action'\r\n        }\r\n    },\r\n];\r\n\r\n\r\nexport default {\r\n  data() {\r\n    return {\r\n        loading : false,\r\n        data : [],\r\n        columns,\r\n        searchText: '',\r\n        searchInput: null,\r\n        searchedColumn: ''\r\n    };\r\n  },\r\n  methods : {\r\n    onSearch(value){\r\n        this.loading = true;\r\n        this.getData(value);\r\n    },\r\n    async getData(value){\r\n        const res = await this.$http.get(`/api/v1/getSearchResult?search_text=${value}`);\r\n        this.data = [...res.data];\r\n        this.loading = false;\r\n    },\r\n    onClick({ key }) {\r\n      console.log(key);\r\n    },\r\n    handleSearch(selectedKeys, confirm, dataIndex) {\r\n      confirm();\r\n      this.searchText = selectedKeys[0];\r\n      this.searchedColumn = dataIndex;\r\n    },\r\n    handleReset(clearFilters) {\r\n      clearFilters();\r\n      this.searchText = '';\r\n    },\r\n  }\r\n};\r\n</script>\r\n<style scope>\r\n    .table-content{\r\n        min-height: 600px;\r\n        background: #fff;\r\n    }\r\n    .search-box{\r\n        margin-bottom: 20px;\r\n    }\r\n    .highlight {\r\n    background-color: rgb(255, 192, 105);\r\n    padding: 0px;\r\n    }\r\n</style>"],"sourceRoot":""}]);

